.App
{
    text-align: center;

    box-shadow: 0 0 5px 0 #00004b1a;
}

.banner
{
    position: relative;

    overflow: hidden;

    min-height: 120vh;
    padding: 0;

    background-color: #1f1f20;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: 50% 50%;
    -webkit-background-size: cover;
       -moz-background-size: cover;
         -o-background-size: cover;
            background-size: cover;
}

.bannerContent
{
    position: relative;
    z-index: 1;

    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;

    min-height: 120vh;
}

.container
{
    display: flex;
    overflow: hidden;
    flex-wrap: wrap;
    justify-content: center;

    box-sizing: border-box;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 15px;
}

.row
{
    flex-direction: row;
    flex-wrap: wrap;

    box-sizing: content-box;

    composes: container;
}


.mainBtn
{
    font-weight: 600;

    display: inline-block;

    margin: 2px 0;
    padding: 5px 5px;

    color: #f50;
    border: solid 1px rgba(255, 85, 0, .99);
    border-radius: 5px;
}

.mainBtn:hover
{
    text-shadow: 0 0 5px rgba(196, 67, 0, .99);

    color: white;
    border-color: rgba(196, 67, 0, .99);
    background-color: rgba(255, 85, 0, .99);
}

.mainBtn:active
{
    color: #fff;
    border-color: inherit;
    background-color: rgb(255, 85, 0);
}

@media (min-width: 1151px)
{
    .container
    {
        max-width: 1100px;
    }

    .App
    {
        max-width: 1280px;
        margin: 0 auto;
    }
}

@media (min-width: 992px) and (max-width: 1150px)
{
    .container
    {
        max-width: 960px;
    }
}

@media (min-width: 768px) and (max-width: 991px)
{
    .container
    {
        max-width: 760px;
    }
}

@media (min-width: 576px) and (max-width: 767px)
{
    .container
    {
        max-width: 540px;
    }

    h2,
    h3
    {
        font-size: 1.8rem;
    }
}

@media (max-width: 576px) and (min-width: 300px)
{
    .container,
    .row
    {
        max-width: 350px;
        padding-right: 5px;
        padding-left: 5px;
    }
}

button
{
    font-weight: 400;

    display: inline-block;

    box-sizing: border-box;
    max-height: 3rem;
    margin: .5rem;
    padding: 8px 20px;

    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    text-decoration: none;

    color: #f50;
    border: solid 1px #999;
    border-radius: .4rem;
    background-color: inherit;
}

button:hover
{
    border-color: rgba(196, 67, 0, .99);
}

link
{
    text-decoration: none;
}

hr
{
    display: block;
    overflow: visible;

    box-sizing: content-box;
    width: 80%;
    height: 1px;
    margin-top: 1rem;
    margin-bottom: 1rem;

    border: 0;
    border-top: 1px solid rgba(0, 0, 0, .1);

    unicode-bidi: isolate;
    margin-block-start: .5em;
    margin-block-end: .5em;
    margin-inline-start: auto;
    margin-inline-end: auto;
}

button:focus
{
    outline: 0;
}

input,
select,
textarea
{
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0 .725rem;
    font-variant: tabular-nums;
    list-style: none;
    -webkit-font-feature-settings: 'tnum';
    font-feature-settings: 'tnum', "tnum";
    position: relative;
    display: inline-block;
    width: 100%;
    height: 32px;
    color: rgba(0, 0, 0, 0.65);
    font-size: 14px;
    line-height: 1.5;
    background-color: #fff;
    background-image: none;
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;

    max-width: 90%;
    margin-top: .5rem;

    color: #495057;
    background-clip: padding-box;
}

textarea
{
    min-width: 10rem;
    min-height: 7rem;
}
